import React from "react";
import { Chip } from "@material-ui/core";
import { Wrapper, CardWrapper, ImageWrapper, DataWrapper } from "./styled";
const Card = ({ card }) => {
  const handleClick = () => {
    window.location = `/${card.title.toLowerCase()}`;
  };
  return (
    <Wrapper onClick={handleClick}>
      <CardWrapper>
        <ImageWrapper style={{ backgroundImage: `url(${card.icon})` }} />
        <DataWrapper>
          <h1 className="data-title">{card.title}</h1>
          <div className="data-tags">
            {card.chips.map(chip => {
              return (
                <Chip
                  label={chip}
                  size="small"
                  color="primary"
                  variant="outlined"
                />
              );
            })}
          </div>
          <p className="data-body">{card.description}</p>
        </DataWrapper>
      </CardWrapper>
    </Wrapper>
  );
};

export default Card;
